
import XSwitch from 'vux/src/components/x-switch/index.vue'
import Group from 'vux/src/components/group/index.vue'


export default {
components: {
XSwitch,
Group
},
methods: {
onClick(newVal, oldVal) {
console.log(newVal, oldVal)
this.$vux.loading.show({
text: 'in processing'
})
setTimeout(() => {
this.$vux.loading.hide()
this.value2 = newVal
}, 1000)
}
},
data() {
return {
value1: true,
value2: false
}
}
}



