
import Qrcode from 'vux/src/components/qrcode/index.vue'
import Divider from 'vux/src/components/divider/index.vue'


export default {
mounted() {
setInterval(() => {
this.value = `https://vux.li?t=${Math.random()}`
this.fgColor = `#${Math.floor(Math.random() * 16777215).toString(16)}`
}, 10000)
},
components: {
Qrcode,
Divider
},
data() {
return {
value: 'https://vux.li',
fgColor: '#000000'
}
}
}



