
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import InlineXNumber from 'vux/src/components/inline-x-number/index.vue'
import InlineLoading from 'vux/src/components/inline-loading/index.vue'
import LoadMore from 'vux/src/components/load-more/index.vue'
import Loading from 'vux/src/components/loading/index.vue'
import XSwitch from 'vux/src/components/x-switch/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'


export default {
name:'MyInlineMore',
directives: {
TransferDom
},
components: {
InlineLoading,
Group,
Cell,
InlineXNumber,
Loading,
LoadMore,
XSwitch,
XButton
},
data() {
return {
show1: false,
text1: 'Processing'
}
},
methods: {
showLoading() {
this.$vux.loading.show({
text: 'Loading'
})
setTimeout(() => {
this.$vux.loading.hide()
}, 2000)
},
show1change(val) {
if (val) {
tick(0, (percent) => {
if (percent === 100) {
this.show1 = false
return
}
this.text1 = `${percent}%`
})
}
}
}
}
function tick(i, cb) {
setTimeout(function () {
i++
cb(i)
if (i < 100) {
tick(i, cb)
}
}, 10)
}




