
import TransferDom from 'vux/src/directives/transfer-dom/index.js'
import Popup from 'vux/src/components/popup/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import XSwitch from 'vux/src/components/x-switch/index.vue'
import Scroller from 'vux/src/components/scroller/index.vue'
import Toast from 'vux/src/components/toast/index.vue'
import XAddress from 'vux/src/components/x-address/index.vue'
import ChinaAddressData from 'vux/src/datas/china_address.json'


export default {
directives: {
TransferDom
},
components: {
Popup,
Group,
Cell,
XSwitch,
Scroller,
Toast,
XAddress,
XButton
},
data() {
return {
addressData: ChinaAddressData,
show: false,
show1: false,
show2: false,
show3: false,
show4: false,
show5: false,
show6: false,
title6: '默认空的',
value6: [],
show7: false,
showToast: false,
show8: false,
show9: false,
show10: false,
show11: false,
show12: false,
show13: false
}
},
methods: {
resetScroller() {
this.$nextTick(() => {
this.$refs.scroller.reset()
})
},
log(str) {
console.log(str)
}
},
watch: {
show10(val) {
if (val) {
setTimeout(() => {
this.show10 = false
}, 1000)
}
}
}
}




