
import XCircle from 'vux/src/components/x-circle/index.vue'
import Range from 'vux/src/components/range/index.vue'
import Icon from 'vux/src/components/icon/index.vue'


export default {
ready() {
setInterval(this.update2, 2000)
},
components: {
XCircle,
Range,
Icon
},
data() {
return {
percent1: 10,
percent2: 30,
strokeColor2: '#3FC7FA'
}
},
methods: {
update2() {
const colorMap = ['#3FC7FA', '#85D262', '#FE8C6A']
this.percent2 = parseInt(Math.random() * 100, 10)
this.strokeColor2 = colorMap[parseInt(Math.random() * 3, 10)]
}
}
}



