
import XButton from 'vux/src/components/x-button/index.vue'
import Range from 'vux/src/components/range/index.vue'
import Group from 'vux/src/components/group/index.vue'
import GroupTitle from 'vux/src/components/group-title/index.vue'
import Cell from 'vux/src/components/cell/index.vue'


export default {
components: {
Range,
Group,
GroupTitle,
Cell,
XButton
},
mounted() {
setTimeout(() => {
this.showData13 = true
}, 2000)
},
data() {
return {
data1: 0,
data2: 0,
data3: 20,
data4: 18,
data5: 28,
data6: 37,
data7: 17,
data8: 25,
data9: 50,
data10: 14,
data11: 30,
data12: 0,
data13: 10,
showData13: false,
min: 0,
max: 100,
step: 1,
dynamiValue: 0
}
},
methods: {
onChange(val) {
console.log('change', val)
},
update() {
this.min = Math.floor(Math.random() * 30)
this.max = Math.floor(50 + Math.random() * 100)
this.step = 1 + Math.floor(Math.random() * 10)
}
}
}




