
import Group from 'vux/src/components/group/index.vue'
import XAddress from 'vux/src/components/x-address/index.vue'
import ChinaAddressV3Data from 'vux/src/datas/china_address_v3.json'
import XButton from 'vux/src/components/x-button/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import value2name from 'vux/src/filters/value2name.js'


export default {
components: {
Group,
XAddress,
XButton,
Cell
},
data() {
return {
title: '默认为空',
value_0_1: [],
value: [],
title2: '设置值',
value2: ['天津市', '市辖区', '和平区'],
value3: ['广东省', '中山市', '--'],
addressData: ChinaAddressV3Data,
value4: [],
value5: ['广东省', '深圳 市', '南山区']
}
},
methods: {
onShadowChange(ids, names) {
console.log(ids, names)
},
changeData() {
this.value2 = ['430000', '430400', '430407']
},
changeDataByLabels() {
this.value2 = ['广东省', '广州市', '天河区']
},
changeDataByLabels2() {
this.value2 = ['广东省', '中山市', '--']
},
getName(value) {
return value2name(value, ChinaAddressV3Data)
},
logHide(str) {
console.log('on-hide', str)
},
logShow(str) {
console.log('on-show')
}
}
}



