
import Confirm from 'vux/src/components/confirm/index.vue'
import Group from 'vux/src/components/group/index.vue'
import XSwitch from 'vux/src/components/x-switch/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'

export default {
directives: {
TransferDom
},
components: {
Confirm,
Group,
XSwitch,
XButton
},
data() {
return {
show: false,
show2: false,
show3: false,
show4: false,
show5: false
}
},
methods: {
onCancel() {
console.log('on cancel')
},
onConfirm(msg) {
console.log('on confirm')
if (msg) {
alert(msg)
}
},
onConfirm4() {
console.log('on confirm')
this.$vux.loading.show({
transition: '',
text: 'processing'
})
setTimeout(() => {
this.$vux.loading.hide()
this.show4 = false
}, 1000)
},
onHide() {
console.log('on hide')
},
onShow() {
console.log('on show')
},

onShow5() {
this.$refs.confirm5.setInputValue('default')
},
onConfirm5(value) {
this.$refs.confirm5.setInputValue('')
this.$vux.toast.text('input value: ' + value)
},
showPlugin() {
this.$vux.confirm.show({
title: 'Title',
content: 'Content',
onShow() {
console.log('plugin show')
},
onHide() {
console.log('plugin hide')
},
onCancel() {
console.log('plugin cancel')
},
onConfirm() {
console.log('plugin confirm')
}
})
},
showPlugin2() {
this.showPlugin()
},
showPlugin3() {
this.$vux.confirm.prompt('123', {
title: 'Title',
onShow() {
console.log('promt show')
},
onHide() {
console.log('prompt hide')
},
onCancel() {
console.log('prompt cancel')
},
onConfirm(msg) {
alert(msg)
}
})
}
}
}




